import generate from '@babel/generator';
import * as t from '@babel/types';
import { parse } from '@babel/parser';
import traverse from '@babel/traverse';
export const additionalJsTexts = [];
export const reatciveIdentifers = new Set();
export function parseJs(js) {
    const ast = parse(js);
    parseLabel(ast);
    parseDotValue(ast);
    return generate(ast).code;
}
export function parseLabel(ast, env = 'browser') {
    traverse(ast, {
        LabeledStatement(path) {
            const { node } = path;
            if (node.label.name !== 'ref')
                return;
            if (!t.isExpressionStatement(node.body))
                return;
            if (!t.isAssignmentExpression(node.body.expression))
                return;
            const { left: identifier, right: value } = node.body.expression;
            identifier.noNeedDotValue = true;
            reatciveIdentifers.add(generate(identifier).code);
            path.replaceWith(t.variableDeclaration('const', [
                t.variableDeclarator(identifier, t.callExpression(t.identifier(env === 'browser' ? 'frame.reactive' : '$_reactive'), [value])),
            ]));
        },
    });
    return ast;
}
export function parseDotValue(ast) {
    traverse(ast, {
        Identifier(path) {
            if (!reatciveIdentifers.has(path.node.name) || path.node.noNeedDotValue)
                return;
            path.replaceWith(t.memberExpression(path.node, t.identifier('value')));
            path.skip();
        },
    });
    return ast;
}
